@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap");
/* Base */
/* Reset and base styles  */
* {
  padding: 0px;
  margin: 0px;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Links */
a, a:link, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

/* Common */
aside, nav, footer, header, section, main {
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: inherit;
  font-weight: inherit;
}

ul, ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

img, svg {
  max-width: 100%;
  height: auto;
}

address {
  font-style: normal;
}

/* Form */
input, textarea, button, select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input::-ms-clear {
  display: none;
}

button, input[type=submit] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
  outline: none;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

legend {
  display: block;
}

:root {
  --container-width: 1170px;
  --container-padding: 15px;
  --font-main: "Montserrat", sans-serif;
  --font-accent: "Inter", serif;
  --page-bg: #fff;
  --text-color: #000;
  --accent: #b49c8f;
  --accent-second: #f6f3f3;
  --laptop-size: 1199px;
  --tablet-size: 768px;
  --mobile-size: 375px;
}

.dark {
  --page-bg: #252526;
  --text-color: #fff;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--page-bg);
  color: var(--text-color);
  font-family: var(--font-main);
}

.none {
  display: none !important;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.no-scroll {
  overflow-y: hidden;
}

.container {
  padding: 0 var(--container-padding);
  max-width: var(--container-width);
  margin: 0 auto;
}

html, body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}


/* Nav Icon */
.mobile-nav-btn {
  --time: 0.1s;
  --width: 20px;
  --height: 10px;
  --line-height: 3px;
  --spacing: 5px;
  --color: var(--accent);
  --radius: 4px;
  /* Fixed height and width */
  /* height: var(--height); */
  /* width: var(--width); */
  /* Dynamic height and width */
  display: none;
}
@media (max-width: 767px) {
  .mobile-nav-btn {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}



/* Layout */
.mobile-nav-btn {
  z-index: 999;
}

@media (max-width: 768px) {
  .mobile-nav-btn {
    display: block;
  }
}
.logo {
  width: 250px;
}
@media (max-width: 850px) {
  .logo {
    width: 130px;
  }
}
@media (max-width: 767px) {
  .logo {
    width: 100px;
  }
}

.logo__img {
  height: 120px;
}
@media (max-width: 850px) {
  .logo__img {
    height: 60px;
    width: 120px;
  }
}
@media (max-width: 767px) {
  .logo__img {
    height: 40px;
    width: 80px;
  }
}

.mobile-nav {
  position: fixed;
  top: -100%;
  width: 100%;
  height: 100%;
  z-index: 99;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background: rgb(130, 156, 168);
  transition: all 0.2s ease-in;
}
.mobile-nav--open {
  position: fixed;
  top: 0;
}
.item {
  font-size: 28px;
  color: white;
}
.item:hover {
  color: aqua;
}







.title {
  font-size: 44px;
  font-weight: 300;
  line-height: 1;
  color: var(--text-color);
  margin-bottom: 50px;
}
@media (max-width: 850px) {
  .title {
    font-size: 24px;
    text-align: center;
  }
}

.portfolio {
  margin-top: 200px;
}
@media (max-width: 850px) {
  .portfolio {
    margin-top: 120px;
  }
}

.portfolio__content {
  display: flex;
  flex-direction: column;
}

.portfolio__two, .portfolio__three {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 20px;
  margin-bottom: 20px;
}

.portfolio__two img {
  width: 600px;
}
@media (max-width: 850px) {
  .portfolio__two img {
    width: 300px;
  }
}

.portfolio__three img {
  width: 393px;
  height: 550px;
}
@media (max-width: 850px) {
  .portfolio__three img {
    width: 193px;
    height: 300px;
  }
}

.socials {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
@media (max-width: 850px) {
  .socials {
    gap: 5px;
  }
}

.socials__list {
  display: flex;
  gap: 20px;
}
@media (max-width: 850px) {
  .socials__list {
    gap: 5px;
  }
}
@media (max-width: 767px) {
  .socials__list {
    justify-content: center;
  }
}

.socials__item {
  width: 50px;
  border-radius: 15px;
}
@media (max-width: 850px) {
  .socials__item {
    width: 30px;
  }
}
.socials__item:hover, .socials__item :focus {
  background-color: #9a7968;
}
@media (max-width: 850px) {
  .socials__item--img {
    width: 30px;
  }
}

.socials__email {
  display: flex;
  align-items: center;
}

.socials__email--img {
  width: 35px;
  margin-right: 10px;
}
@media (max-width: 850px) {
  .socials__email--img {
    width: 25px;
  }
}
@media (max-width: 767px) {
  .socials__email--img {
    width: 18px;
  }
}

.socials__email--text {
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  color: var(--accent-second);
  text-transform: uppercase;
  cursor: pointer;
  padding-bottom: 0;
}
@media (max-width: 850px) {
  .socials__email--text {
    font-size: 10px;
    padding-bottom: 12px;
  }
}
@media (max-width: 767px) {
  .socials__email--text {
    font-size: 8px;
    padding-bottom: 20px;
  }
}
.socials__email--text:hover, .socials__email--text :focus {
  color: #9a7968;
}

.price {
  width: 1190px;
  margin-top: 150px;
  margin-bottom: 15px;
  padding: 150px 150px 100px;
  background-color: rgba(204, 176, 161, 0.6274509804);
  background-image: url(./../img/price/marble.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  text-transform: uppercase;
}
@media (max-width: 850px) {
  .price {
    max-width: 768px;
    margin-top: 100px;
  }
}

.price__box {
  padding: 30px;
  background-color: rgba(246, 243, 243, 0.6117647059);
}

.price__title {
  font-size: 56px;
  font-weight: 400;
  text-align: center;
}
@media (max-width: 850px) {
  .price__title {
    font-size: 28px;
  }
}

.price__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 60px;
}
@media (max-width: 850px) {
  .price__list {
    gap: 30px;
  }
}

.price__list--box {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 60px;
  margin-bottom: 60px;
}
@media (max-width: 850px) {
  .price__list--box {
    flex-direction: column;
  }
}

.price__item--last {
  width: 350px;
}
@media (max-width: 850px) {
  .price__item--last {
    width: 400px;
  }
}

.price__subtitle {
  display: flex;
  justify-content: space-between;
  font-size: 24px;
  font-weight: 400;
  line-height: 1;
  border-bottom: solid 1px;
  margin-bottom: 15px;
}
@media (max-width: 850px) {
  .price__subtitle {
    font-size: 16px;
  }
}

.price__span--text {
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
}

.description__item {
  list-style: disc;
  margin-left: 30px;
  font-size: 16px;
}
@media (max-width: 850px) {
  .description__item {
    font-size: 12px;
  }
}

.contact {
  margin-top: 280px;
  margin-bottom: 220px;
  display: flex;
  align-items: center;
  text-transform: uppercase;
}
@media (max-width: 850px) {
  .contact {
    margin-top: 180px;
    margin-bottom: 130px;
  }
}

.contact__left {
  margin-right: 120px;
}
@media (max-width: 850px) {
  .contact__left {
    margin-right: 60px;
  }
}

.contact__img {
  width: 650px;
  filter: grayscale(100%);
}
@media (max-width: 850px) {
  .contact__img {
    width: 350px;
  }
}

.contact__right {
  width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
@media (max-width: 850px) {
  .contact__right {
    width: 200px;
  }
}

.contact__list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 30px;
}

.contact__item {
  display: flex;
  align-items: center;
}

.contact__icon {
  width: 30px;
  margin-right: 20px;
}
@media (max-width: 850px) {
  .contact__icon {
    width: 20px;
  }
}

.contact__address {
  font-size: 24px;
  font-weight: 400;
  color: var(--accent);
  line-height: 1;
}
@media (max-width: 850px) {
  .contact__address {
    font-size: 12px;
  }
}

.contact__mail {
  font-size: 20px;
  font-weight: 400;
  color: var(--accent);
  line-height: 1;
}
@media (max-width: 850px) {
  .contact__mail {
    font-size: 12px;
  }
}

@media (max-width: 767px) {
  .change-lang__desktop {
    display: none;
  }
}
