.nav__list{
	display: flex;
	flex-direction: row;
	align-items: center;
	gap: 60px;

	@include tablet{
	gap: 20px;
	}

	@include mobile{
		display: none;
		}
}

.nav__link{
	font-size: 16px;
	font-weight: 400;
	line-height: 1;
	color: var(--text-color);
	text-transform: uppercase;

    &:hover, :focus{
		color: var(--accent);
	}

	@include tablet{
		font-size: 12px;

	}
}