.hero{
    width: 100%;
    margin-top: 130px;
    padding: 200px 0;
    background-color: var(--accent);

    @include tablet{
    margin-top: 80px;
    padding: 150px 0;
    }

    @include mobile{
    // width: 350px;
        margin-top: 80px;
        padding: 150px 0;
        }
}

.hero__title{
    margin-bottom: 40px;
    font-size: 52px;
font-weight: 300;
line-height: 1;
color: var(--page-bg);
text-align: center;

@include tablet{
    font-size: 34px;
}

@include mobile{
    width: 300px;
    font-size: 20px;
}
}

.hero__text{
    // width: 600px;
    margin-left: auto;
    margin-right: auto;
    font-size: 24px;
    font-weight: 300;
    line-height: 1.4;
    color: var(--page-bg);
    text-align: center;


&--author{
    // width: 200px;
