.about{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 80px;
    padding: 120px 0;

    @include tablet{
        gap: 60px;
        padding: 60px 0;
    }

    @include mobile{
        flex-direction: column;
    }
}

.about__right {
    display: flex;
    flex-direction: row;
    align-items: center;
}
.about__rotate {
    font-size:  12px ;
  letter-spacing: 2px;
  text-transform: uppercase;
  padding: 5px 10px;
  margin: 0 0 10px 0;
  line-height: 2;
  color: var(--accent);

  &--left{
    writing-mode:vertical-rl;
    rotate: 180deg;
 }

  &--right{
     writing-mode:vertical-rl;
  }
 
  @include tablet{
    font-size:  10px ;
  }
}
.about__img{
    width: 500px;

    @include tablet{
        width: 280px;
    }

    @include mobile{
        width: 220px;
    }
}
.about__left {
    width: 550px;

    @include tablet{
        width: 350px;
    }

    @include mobile{
        width: 300px;
    }
}

.about__text {
    font-size: 20px;
    font-weight: 400;
    line-height: 1.5;
    text-align: center;

    &--up, &--middle{
        margin-bottom: 40px;

        @include tablet{
        margin-bottom: 28px;
        }
    }

    @include tablet{
    font-size: 14px;

    }

}
