.header {
    width: 100%;
    position: fixed;
    top: 0;
    background-color: var(--page-bg);
    padding: 5px 0;
    z-index: 999;
  }
  .header__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  @media (max-width: 767px) {
    .header__row {
      justify-content: start;
    }
  }
  
  .header__functionality {
    display: flex;
    gap: 10px;
    margin-left: auto;
  }